import React,{useState} from 'react'

export default function Form(props) {
const [text,setText] = useState("Write Somthing here")
const [cpy, setCpy] = useState("Copy")
const handalupper = () =>{
    console.log('upper cliked')
    setText(text.toUpperCase())
}
const handalOnchange=(event)=>{
    console.log("Onchange Cliked")
   setText(event.target.value);
   setCpy("Copy")
}
const handalLower=()=>{
    setText(text.toLowerCase())
}
const handalTitle =() =>{
    // setText("hi")
    let txt =text.toLowerCase().split(" ") 
    console.log(txt)
    for (let t=0 ; t< txt.length ; t++) {
        txt[t] =txt[t][0].toUpperCase()+txt[t].slice(1) ;

    }
   let ffg =  txt.join(" ");
    setText(ffg); 

}
const handalCopy =() =>{
    // setText("hi")
    

        let copyTextarea = document.querySelector('#myText');
        copyTextarea.focus();
        copyTextarea.select();
        try {
            document.execCommand('copy');
   setCpy("Copied")
            // console.log("copied");
            // setcpt("Copied");
        // props.showAlert("Copy text  ","success");


          } catch(err) {
            setCpy("Try again")
        // props.showAlert("Error Occur in Copying ","warning");

          }

    }
const handalClear =() =>{
    setText("")
}

function download(filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);
  
    element.style.display = 'none';
    document.body.appendChild(element);
  
    element.click();
  
    document.body.removeChild(element);
  }
  
  // Start file download.
//   download("hello.txt","This is the content of my file :)");

const handalDowload =() =>{
  download("Txtaction.txt",text);

      };

const wordCount =()=>{
   let tt = text.split(" ")
   let count = 0;
   for(let i =0 ; i<tt.length ; i++){
    if(tt[i]!==""){
        count = count+1 ;
    }
   } 
    return count ;
} 
const charCount=()=>{
    let yy = text.split(" ")
    let j =0 ;
    let kk =[]
    for(let i=0 ; i<yy.length ; i++){
        if(yy[i] !== ""){
            kk[j] = yy[i];
            j = j+1 ;
        }
    }
   return(kk.join('').length)
}

const handalExtra =()=>{
    let yy = text.split(" ")
    let j =0 ;
    let kk =[]
    for(let i=0 ; i<yy.length ; i++){
        if(yy[i] !== ""){
            kk[j] = yy[i];
            j = j+1 ;
        }
    }
    setText(kk.join(" "))
}
  return (
    <>
<div className="container">
<div className="mb-3">
    <h1>{props.heading}  </h1>
  <textarea className="form-control" onChange={handalOnchange} value={text} id="myText" rows="10" ></textarea>
 
</div>
<div className="hcont">
<button type="button" onClick={handalupper} className="btn btn-primary mx-2">Convert UpperCase</button>
<button type="button" onClick={handalLower} className="btn btn-primary mx-2">Convert LowerCase</button>
<button type="button" onClick={handalTitle} className="btn btn-primary mx-2">Convert TitleCase</button>
<button type="button" onClick={handalCopy} className="btn btn-primary mx-2">{cpy}</button>
<button type="button" onClick={handalClear} className="btn btn-primary mx-2">Clear Text</button>

<button type="button" onClick={handalDowload} className="btn btn-primary mx-2">Dowload</button>
<button type="button" onClick={handalExtra} className="btn btn-primary mx-2">Extra Spaces Remove</button>

</div>
</div>
<div className="container">
    <h1>PriView :</h1>
    <h4>Number of Word  :{wordCount()  }</h4>
    <h4>Number of Charactor :{charCount()}</h4>
    <p>{(text === "Write Somthing here") ||(text===" ")?"No Preview Available!": text}</p>

</div>
    </>
  )
}
